.App {
  text-align: center;
  height: 100%;
  margin-bottom: 100px !important;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

html,
body,
#root {
  height: 100%;
  /* overflow: auto; */
  margin-bottom: 100px !important;
}

.sideBarItemContainer {
  display: flex;
  align-items: center;
  padding-left: 30%;
  cursor: pointer;
  height: 60px;
}

.sideBarItemContainer > div {
  font-style: italic;
  margin: 5px;
}

.sideBarItemContainer:hover,
.sideBarItemSelected {
  background-color: white;
  box-shadow: 2px 2px 2px 2px grey;
}

.sideBarItemContainer:hover,
.sideBarItemSelected > div {
  color: black;
}

@media (max-width: 800px) {
  .sideBarItemContainer > div {
    display: none;
  }
}

@media (min-width: 900px) {
  .sideBarItemContainer > div {
    font-size: 14px;
  }
}
.notFoundContainer {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: red;
  font-size: 35px;
  font-style: italic;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Roboto Condensed", sans-serif;
}

