.flexBox > div {
  margin-bottom: 25px;
}

.mailHover {
  /* text-decoration: none; */
  color: black;
}

.mailHover:hover {
  text-decoration: underline !important;
}

@media (max-width: 900px) {
  .personalInfoWrapper {
    grid-template-rows: 1fr 1fr !important;
    grid-template-columns: 1fr !important;
    display: flex !important;
    flex-direction: column;
    justify-content: space-between;
  }
  .socialAndDescription {
    grid-row: 1;
    grid-column: 1;
  }

  .skillsContainer {
    grid-row: 2;
    grid-column: 1 !important;
    /* margin-top: 45px; */
  }

  .contactDetails {
    grid-template-columns: repeat(1, 1fr) !important;
  }

  .companyLogo {
    width: 150px;
  }

  .commonGridContainer {
    grid-template-columns: 1fr 1fr !important;
    display: grid;
  }

  .commonGridWrapper {
    grid-column: 1/3 !important;
  }

  .companyDescriptionText {
    grid-column: 1/3 !important;
    grid-row: 2;
  }
}

.commonGridContainer {
  display: grid;
  grid-template-columns: 1fr 3fr 3fr 1fr;
  grid-gap: 25px;
}

.commonGridWrapper {
  grid-column: 2/4;
}

.companyName {
  font-size: 40px;
  font-stretch: expanded;
  /* font-display: ; */
  color: #292961;
  font-weight: bold;
}

.companyTenure {
  color: #757575;
  /* padding-left: 15px; */
  margin-top: 10px;
}

.companyDescriptionText {
  grid-column: 2/3;
  grid-row: 2;
}

p {
  margin: 0px;
}

.projectDescription {
  margin-top: 25px;
}

